export default {
	data() {
		return {
			stopLoad: false,
			page: 1,
			goods: []
		}
	},
	onLoad() {
		this.getGoodsList()
	},
	methods: {
		toGoodsDetail(id) {
			uni.navigateTo({
				url: "../integralGoodsDetail/integralGoodsDetail?id=" + id
			})
		},
		getGoodsList() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/goods/integralMall',
				data: {
					token: uni.getStorageSync('token'),
					type: 0,
					page: this.page,
					offset: 10
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.goods = res.data.data.goods
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		loadMore() {
			if (this.stopLoad) {
				return
			}
			this.page++
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/goods/integralMall',
				data: {
					token: uni.getStorageSync('token'),
					type:0,
					page: this.page,
					offset: 10
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.goods.length == 0) {
							this.stopLoad = true
							return
						}
						for (var i = 0; i < res.data.data.goods.length; i++) {
							this.goods.push(res.data.data.goods[i])
						}
						return
					}
					this.showToast(res.data.msg)
				}
			})
		}
	},
	//页面触底加载更多精选商品
	onReachBottom() {
		this.loadMore()
	},
}
